<template>
  <div class="">
    <v-head></v-head>
    <big-img v-if="showImg" @clickit="viewImg" :imgSrc="imgSrc"></big-img>
    <div class="content">
      <div class="result-cont">
        <p class="res-tit">查询结果</p>
        <table class="table">
          <thead>
          <tr>
            <th>证书编号</th>
            <th>证书名称</th>
            <th>身份证号</th>
            <th>姓名</th>
          </tr>
          </thead>
          <tr>
            <td>{{result.cert_sn}}</td>
            <td>{{result.cert_name}}</td>
            <td>{{result.cardId}}</td>
            <td>{{result.name}}</td>
          </tr>
        </table>
      </div>
      <div class="img-cont">
        <p class="img-cont-load">
          <a :href="loadPdfUrl" download="w3logo">点击下载 (pdf格式)</a>
        </p>
        <i class="img-cont-i">
          <!--<img src="../assets/img/cert.png" @click="clickImg($event)">-->
          <img :src="imgUrl" @click="clickImg($event)">
        </i>
        <p class="img-cont-p">
          <span><img src="../assets/img/search.png"></span>
          <a href="javascript:;">点击查看大图</a>
        </p>
      </div>
      <!--<transition name="move" mode="out-in">-->
        <!--<router-view></router-view>-->
      <!--</transition>-->
    </div>
    <v-footer></v-footer>
  </div>
</template>

<script>
  import vHead from '../components/Header'
  import vFooter from '../components/Footer'
  import BigImg from '../components/BigImg'
  export default {
    name: 'Home',
    components: {
      vHead, vFooter,BigImg
    },
    data() {
      return {
        cert_id:'',
        routeParam:'',

        imgUrl:'',
        loadPdfUrl:'',

        result:{
          cert_sn:'123456',
          cert_name:'中级工程师',
          cardId:'1309999999584752635',
          name:'李工'
        },
        showImg:false,
        imgSrc: ''

      }
    },
    created(){
      this.getData();
    },
    methods:{
      getData(){
        let self = this;
        self.cert_id = self.$route.query.cert_id;
        self.routeParam = self.$route.query.param;

        self.$axios.get(self.baseUrl+'/api/e-certs/'+self.cert_id+'/'+self.routeParam).then(function(res){
          if(res.data.success){
            let data = res.data.data;
            self.result.cert_sn = data.cert_sn;
            self.result.cardId = data.user_id;
            self.result.cert_name = data.cert_name;
            self.result.name = data.user_name;

            self.imgUrl = data.image_url;
            self.loadPdfUrl = data.pdf_url;

          }else{
            self.$toast.center(res.data.msg);
          }
        })

      },
      clickImg(e) {
        this.showImg = true;
        // 获取当前图片地址
        console.log(e);
        this.imgSrc = e.currentTarget.src;
      },
      viewImg(){
        this.showImg = false;
      },

    }
  }
</script>
<style scoped>
  .content {
    background: none repeat scroll 0 0 #fff;
    width: 1000px;
    margin: 0 auto;
    border-top: 2px solid #D19421;
    padding: 40px;
  }
  .res-tit{
    font-size:16px;
    color:#333;
    line-height:38px;
  }
  .table {
    width:100%;
    box-sizing: border-box;
    border-collapse: collapse;
    margin: 0 auto;
    text-align: center;
    color:#333;
    font-size:14px;
  }

  .table td, table th {
    border: 1px solid #ddd;
    color: #333;
    height: 44px;
  }

  .table thead th {
    background-color: #f1f1f1;
    /*width: 100px;*/
    height:44px;
  }

  .table tr:nth-child(odd) {
    background: #fff;
  }

  table tr:nth-child(even) {
    background: #fff;
  }

  .img-cont{
    margin:46px auto 30px;
    text-align: center;
  }
  .img-cont-i{
    display: inline-block;
    max-width:350px;
    border:1px solid #eee;
    cursor: pointer;
  }
  .img-cont-i img{
    width:100%;
  }
  .img-cont-p{
    margin:20px auto 0;
  }
  .img-cont-p img{
    vertical-align: middle;
  }
  .img-cont-p a{
    font-size: 14px;
    color: #666666;
    cursor: text;
    letter-spacing: 1.71px;
  }
  .img-cont-load{
    margin:0 auto 20px;
    font-size: 14px;
    color: #666666;
    cursor: text;
    letter-spacing: 1.71px;
  }


</style>



